import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);
//
const login = () =>
	import('./template/login/login.vue'); //
const index = () =>
	import('./template/index/index.vue'); //
const Mobilechat = () =>
	import('./template/Mobilechat/Mobilechat.vue'); //Mobilechat
const chart = () =>
	import('./template/chart/chart.vue'); //
const serviceRecord = () =>
	import('./template/serviceRecord/serviceRecord.vue'); //
const plogin = () =>
	import('./template/plogin/plogin.vue'); //
const allworkordermanagement = () =>
	import('./template/allworkordermanagement/allworkordermanagement.vue'); //
const Istarted = () =>
	import('./template/Istarted/Istarted.vue'); //
const Idealwith = () =>
	import('./template/Idealwith/Idealwith.vue'); //
const Leave = () =>
	import('./template/Leave/Leave.vue'); //
const allqualitytest = () =>
	import('./template/allqualitytest/allqualitytest.vue'); //
const callenter = () =>
		import('./template/callenter/callenter.vue'); //
const callserverRecord = () =>
				import('./template/callserverRecord/callserverRecord.vue'); //
//
const basepath = '/';
const router = new Router({
	mode: 'hash',
	//
	scrollBehavior(to, from, savedPosition) {
		return {
			x: 0,
			y: 0
		}
	},
	caseSensitive: true,
	routes: [
		//
		{
			path: '*',
			redirect: '/login',
      component: login
		},
		{
			path: '/',
			name: 'login',
			component: login
		},
		{
			path: '/index',
			name: 'index',
			component: index,
			children: [{
					name: 'chart',
					path: '/index/chart',
					component: chart
				},
				{
					name: 'serviceRecord',
					path: '/index/serviceRecord',
					component: serviceRecord
				},
				{
					name: 'allworkordermanagement',
					path: '/index/allworkordermanagement',
					component: allworkordermanagement
				},
				{
					name: 'Istarted',
					path: '/index/Istarted',
					component: Istarted
				},
				{
					name: 'Idealwith',
					path: '/index/Idealwith',
					component: Idealwith
				},
				{
					name: 'leaveword',
					path: '/index/leaveword',
					component: Leave
				},
				{
					name: 'allqualitytest',
					path: '/index/allqualitytest',
					component: allqualitytest
				},
				{
					name: 'callenter',
					path: '/index/callenter',
					component: callenter
				},
				{
					name: 'callserverRecord',
					path: '/index/callserverRecord',
					component: callserverRecord
				},
			]
		},
		{
			path: '/Mobilechat',
			name: 'Mobilechat',
			component: Mobilechat,
		},
		{
			path: '/plogin',
			name: 'plogin',
			component: plogin
		},
	]
});
export default router



